*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    overflow-x: hidden;
  }
  
html{
    
}
.head{
    width: 100vw;
    height: 250px;
    display: block;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.darkmode{
    filter: invert(1) hue-rotate(180deg);
    transition: .05s ease-in-out;
}
.darkmode .videoTag ,.darkmode h1,.darkmode .modnoturne h4 {
    filter: invert(1) hue-rotate(180deg);
    transition: .05s ease-in-out;
}

 .videoTag{
    width: 100%; 
    display: flex;
    top: 0;
    position: absolute;
    z-index: -11;
}

@media (max-width:500px){
    .videoTag{
        width: 200%;
        transform: translateX(-50%);
    }
    
}
.modnoturne{
    align-items: center;
    display: flex;
    position: absolute;
    top: 5px;
    right: 40%;
}
.modnoturne h4{
    padding-right: 15px;
    color: white;
}
.modnoturne svg{
    transition: 0.2s ease-in-out;
    cursor: pointer;
    color: white;
}
.modnoturne svg:hover{
    transform: scale(1.1);
}
.container{
    /*background-image: url(../src/img/mapa.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;*/
    background-color: white;
    height: auto;
    min-height: 100vh;
    display:flex ;
    align-items: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    transition: .05s ease-in-out;
}

