.title{
    font-size: 60px;
    color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    top: 100px;
    position: relative;
    transition: .05s ease-in-out;
}
h4{
    padding-top: 20px;
}
h4 span{
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
h4 span:hover{
   font-size: 24px;
}



.containerInput{
    margin: 15px 0;
    padding: 15px;
    display: flex;
    background-color: transparent;
    border-radius: 8px;
    background-color: rgba(31, 31, 31, 0.7);
    width: 240px;
}
.containerInput input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: rgb(230, 230, 230);
    outline: none;
    margin-right: 8px;
}
.containerInput input::placeholder,.containerInputUf input::placeholder,
.containerInputCidade input::placeholder
,.containerInputLog input::placeholder{
    color: rgb(230, 230, 230);
}

.containerInputUf,.containerInputCidade
,.containerInputLog{
    margin: 5px 0;
    padding: 15px;
    display: flex;
    background-color: transparent;
    border-radius: 8px;
    background-color: rgba(31, 31, 31, 0.7);
    
}
.containerInputUf input,.containerInputCidade input
,.containerInputLog input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: rgb(230, 230, 230);
    outline: none;
    margin-right: 8px;
    width: 100%;
}
.containerInputLog,.containerInputCidade {
    max-width: 500px;
    width: calc( 100vw - 40px);
    height: auto;
}

.buttonSeacrch{
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    cursor: pointer;
    transition: transform 0.2s;
    color: black;
    position: relative;
    left: 5px;
}
.buttonReset{
    padding-left: 5px;
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s;
    color: black;
}
.buttonSeacrch:hover,.buttonReset:hover{
    transform: scale(1.2);
}
.buttonReset:hover{
    transform: scale(1.08);
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 80vw;
    max-width: 400px;
    min-width: 360px;
    
    border-radius: 8px;
}



.main h2 {
    margin: 16px 0;
    font-size: 39px;
    color:rgb(70, 70, 70);
}

.main span, h4{
    margin-bottom: 16px;
    font-weight: bold;
    color: rgb(70, 70, 70);
}
.main span{
    width: 100vw;
    text-align: center;
}

#lista{
    height: auto;
    background-color:transparent;
    display: block;
    margin-bottom: 50px;
}

#lista li{
    list-style: square;
    color:rgb(70, 70, 70);
    padding-top: 10px;
    font-weight: bold;
}
#lista span,#lista p{
    color:rgb(70, 70, 70); 
    padding-right: 5px;
    padding-bottom: 5px;
}

footer{
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
footer h2{
    margin-bottom: 5px;
    font-size: 12px;
    color: white;
    text-shadow: 2px 2px 8px black;
}
footer a{
    margin-left: 7px;
    color: white;
    text-shadow: 2px 2px 8px black;
}