footer{
    background-color: white;
    height: 40px;
    width: 100vw;
    border-top: 1px solid black;
}

.list-dev{
    display: flex;
    align-items: center;
    font-size: 10px;
    padding: 5px;
}
.list-dev>span{
    font-size: 10px;
    color: black;
}
li{
    cursor: pointer;
}
.container-list li:hover{
    transform: scale(1.1);
    transition: 0.2s ease-in;
}
.list-footer img,.list-footer2 img,
.list-footer3 img{
    height: 60px;
}

.list-dev svg{
    color:rgb(0, 0, 0) ;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
.list-dev svg:hover{
    color:rgb(97, 97, 97);
    transform: scale(1.1);
}